import DropperGameComponent from './dropper_game_component';
import defaultOpts from './default_priceless_pourer_opts';

export default function (props, ref, key) {
    return DropperGameComponent(props, ref, key, _.defaults({
        level: 2,
        scoreToWin: 760,
    }, defaultOpts));
}



